.features {
  width: 75%;
  height: 20vh;
  border-radius: 25px;
  background: #fff;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.08);
  margin: 6.5vh auto;
  text-align: left;
}

.features p {
  color: #858585;
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.features h3 {
  color: #191919;
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.features span p {
  color: #858585;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  /* padding-left: 1rem; */
}

.features-grid {
  display: grid;
  justify-items: center;
  grid-auto-columns: 1fr;
  grid-auto-rows: 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 0px 0px;
}

.features-flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 6vh;
  padding: 1vh;
}

.features hr {
  margin-top: 0;
}

.features-grid small {
  display: flex;
  align-items: center;
  gap: 1rem;
  height: 30%;
}